.container {
  position: relative;
  height: calc(100vh - 56px);
  scroll-snap-type: y mandatory;
  overflow: scroll;
  background: linear-gradient(to bottom, #2f0707, #161e30);
}
/* Hide scrollbar for Chrome, Safari and Opera */
.container::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.container {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.video {
  position: relative;
  width: 100%;
  height: 100%;
  scroll-snap-align: start;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 16px 0; /* 避免內容貼近邊界 */
  box-sizing: border-box;
}

.video__wrapper {
  position: relative;
}

.video__player {
  width: 100%;
}

.video__controls {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s, transform 0.3s;
  opacity: 1;
  pointer-events: none;
}

.video__controls.show {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}

.video__controls.hide {
  opacity: 0;
  transform: translate(-50%, -50%) scale(0.8);
}

.video__controls .fa-icon {
  font-size: 24px; /* 確保icon大小合適 */
  color: white;
}

.iconWrapper {
  background-color: rgba(0, 0, 0, 0.5); /* 半灰色透明背景 */
  border-radius: 50%; /* 圓形背景 */
  width: 40px; /* 設定固定寬度 */
  height: 40px; /* 設定固定高度 */
  display: flex;
  align-items: center; /* 垂直置中 */
  justify-content: center; /* 水平置中 */
  transition: transform 0.3s ease, opacity 0.3s ease; /* 過渡效果 */
}

@keyframes scaleIn {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.description {
  display: -webkit-box;
  -webkit-line-clamp: 4; /* 預設顯示四行 */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.5;
}

.tags {
  max-height: 5rem;
  overflow: hidden;
}

@media screen and (min-height: 768px) {
  .description {
    -webkit-line-clamp: 6; /* 螢幕較大時顯示更多行 */
  }
  .tags {
    max-height: none;
    overflow: visible;
  }
}
